header {
  background: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  height: 64px;
  margin: auto;
  padding: 18px 20px;
  display: block;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  display: none;
}

#sign-button {
  display: none;
}

.nav-wrapper {
  margin-left: 0;
  display: flex;
}

.logo {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo-icon {
  width: 24px;
  height: 24px;
}

.logo-text {
  width: 77px;
  height: 13px;
}

.menu-toggle {
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  min-width: 24px;
  min-height: 24px;
  margin: 0;
  padding: 0;
  display: flex;
}

.switch {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  background-color: var(--background-light);
  border-radius: 50%;
  width: 18px;
  height: 18px;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
}

input:checked + .slider {
  background-color: var(--purple-accent-color);
}

input:checked + .slider:before {
  transform: translateX(24px);
}

.hamburger {
  stroke: var(--black-text-color);
  background-color: #0000;
  border: none;
  justify-content: end;
  width: 28px;
  height: 28px;
  padding: 0;
}

.log-in {
  border: 1.5px solid var(--black-text-color);
  background: var(--purple-accent-color);
  color: #fff;
  letter-spacing: -.56px;
  border-radius: 18px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 90px;
  width: 184px;
  height: 48px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
}

.flex {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.btns-header-wrapper {
  align-items: center;
  gap: 14px;
  display: flex;
}

@media screen and (min-width: 767px) {
  .header-list {
    flex-direction: row;
    align-items: center;
    display: flex;
  }

  #sign-button {
    display: flex;
  }

  .hamburger {
    display: none;
  }
}

